<template>
  <div class="footer">
    <div class="footer-left">
      <div
        class="count"
        v-if="cTime">
        <VanCountDown
          :time="time"
          :format="format"
          @change="countChange">
          <template #default="timeData">
            <span class="countTime countSplit">{{ timeData.hours > 9 ? timeData.hours : '0' + timeData.hours }}</span>
            <span class="countSplit">:</span>
            <span class="countTime countSplit">{{ timeData.minutes > 9 ? timeData.minutes : '0' + timeData.minutes }}</span>
            <span class="countSplit">:</span>
            <span class="countTime countSplit">{{ timeData.seconds > 9 ? timeData.seconds : '0' + timeData.seconds }}</span>
            <span>后恢复原价</span>
          </template>
        </VanCountDown>
      </div>
      <div v-else class="countover">
        <p>现价<span class="price">￥12</span></p>
      </div>
    </div>
    <div class="footer-submit" @click="submit">
      {{ cTime ? '立即抢购' : '立即报名' }}
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  props: {
    localKey: {
      type: String,
      default: 'SceneText12-time'
    },
    format: {
      type: String,
      default: 'HH:mm:ss'
    }
  },
  data () {
    return {
      cTime: 0,
      time: localStorage.getItem(this.localKey) === null ? 10 * 60 * 1000 : Number(localStorage.getItem(this.localKey))
    }
  },
  computed: {},
  watch: {},
  created () {
    if (localStorage.getItem(this.localKey) === '0') {
      this.$emit('finish')
    }
  },
  mounted () {},
  methods: {
    // 倒计时变化
    countChange ({ minutes, seconds, milliseconds }) {
      this.cTime = minutes * 60 * 1000 + seconds * 1000 + milliseconds
      localStorage.setItem(this.localKey, this.cTime)
      if (this.cTime === 0) {
        this.$emit('finish')
      }
    },
    submit () {
      this.cTime ? this.$emit('submit') : this.$emit('submit')
    }
  }
}
</script>
<style lang="scss" scoped>
.footer {
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  width: 100%;
  width: 750px;
  height: 150px;
  background: url('~@/assets/Scene/footerBg.png') no-repeat top/contain;
  font-family: PingFangSC-Light, PingFang SC;

  &-left {
    align-self: flex-end;
    height: 109px;
    width: 52%;

    .count {
      display: flex;
      align-items: center;
      justify-content: center;
      background: #fff;
      height: 100%;
    }

    .countTime {
      display: inline-block;
      width: 48px;
      height: 48px;
      line-height: 48px;
      text-align: center;
      background: rgba(255, 228, 110, 0.1);
      border-radius: 7px;
    }

    .countSplit {
      color: #ff2300;
      margin-right: 10px;
      font-weight: 600;
      font-size: 29px;
    }
    // 倒计时结束之后的样式
    .countover {
      height: 100%;
      color: #333;
      padding: 14px 0;
      display: flex;
      align-items: center;
      justify-content: center;
      p:last-child {
        font-size: 32px;
        font-weight: 500;
      }
      .price {
        font-size: 36px;
        font-weight: 600;
        color: #ff2300;
      }
    }
  }

  &-submit {
    align-self: flex-end;
    width: 360px;
    height: 140px;
    line-height: 158px;
    text-align: center;
    font-size: 44px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 700;
    color: #fff;
    text-shadow: 0 4px 2px rgba(227, 38, 0, 0.6);
    animation: btnScale 1s infinite;
  }
}

@keyframes btnScale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
</style>
